'use client';

import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { AlertCircle, PlusCircle, User, Stethoscope, Pill, Hash, Loader2, Trash2 } from 'lucide-react';
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from '@/components/ui/accordion';
import { Badge } from '@/components/ui/badge';
import { checkInteractions } from '@/app/actions';
import { type DetectDrugInteractionsOutput } from '@/ai/flows/detect-drug-interactions';

interface Medication {
  id: number;
  name: string;
  rxNumber: string;
  doctor: string;
  pills: number;
  dosage: string;
  frequency: string;
  form: string;
}

const initialMedications: Medication[] = [
  { id: 1, name: 'Lisinopril', rxNumber: 'RX789123', doctor: 'Dr. Smith', pills: 30, dosage: '10mg', frequency: 'Once daily', form: 'Tablet' },
  { id: 2, name: 'Metformin', rxNumber: 'RX456789', doctor: 'Dr. Smith', pills: 60, dosage: '500mg', frequency: 'Twice daily', form: 'Tablet' },
  { id: 3, name: 'Simvastatin', rxNumber: 'RX123456', doctor: 'Dr. Jones', pills: 30, dosage: '20mg', frequency: 'Once daily', form: 'Tablet' },
];

export function PatientDashboard() {
  const [patientName, setPatientName] = useState('Jane Doe');
  const [membershipNumber, setMembershipNumber] = useState('MEM123456789');
  const [medications, setMedications] = useState<Medication[]>(initialMedications);
  const [newDrugName, setNewDrugName] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [interactions, setInteractions] = useState<DetectDrugInteractionsOutput['interactions']>([]);
  const { toast } = useToast();

  useEffect(() => {
    if (medications.length > 1) {
      runInteractionCheck(medications.map(m => m.name));
    }
  }, []);

  const runInteractionCheck = async (drugList: string[]) => {
    if (drugList.length < 2) return;
    setIsLoading(true);
    const response = await checkInteractions(drugList);
    if (response && 'error' in response) {
      toast({ variant: 'destructive', title: 'Error', description: response.error });
      setInteractions([]);
    } else {
      setInteractions(response.interactions || []);
      if (response.interactions && response.interactions.length > 0) {
        toast({ title: 'Interaction Alert', description: 'A potential drug interaction was detected. Please review your medications.' });
      }
    }
    setIsLoading(false);
  };
  
  const handleAddDrug = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!newDrugName) {
      toast({ variant: 'destructive', title: 'Error', description: 'Please enter a drug name.' });
      return;
    }
    const newMed: Medication = {
      id: Date.now(),
      name: newDrugName,
      rxNumber: `RX${Date.now().toString().slice(-6)}`,
      doctor: 'Dr. Self-Prescribed',
      pills: 0,
      dosage: 'N/A',
      frequency: 'N/A',
      form: 'Unknown'
    };
    
    const updatedMeds = [...medications, newMed];
    setMedications(updatedMeds);
    setNewDrugName('');
    
    await runInteractionCheck(updatedMeds.map(m => m.name));
  };

  const removeDrug = (id: number) => {
    const updatedMeds = medications.filter(med => med.id !== id);
    setMedications(updatedMeds);
    runInteractionCheck(updatedMeds.map(m => m.name));
  };


  const getInteractionStatus = (drugName: string) => {
    return interactions.some(int => int.drugA.toLowerCase() === drugName.toLowerCase() || int.drugB.toLowerCase() === drugName.toLowerCase());
  };

  return (
    <div className="grid grid-cols-1 lg:grid-cols-3 gap-8 items-start">
      <div className="lg:col-span-1 space-y-6">
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2"><User /> Patient Information</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-2">
                <Label htmlFor="patientName">Patient Name</Label>
                <Input 
                    id="patientName" 
                    value={patientName} 
                    onChange={(e) => setPatientName(e.target.value)}
                    placeholder="Enter patient name"
                />
            </div>
            <div className="space-y-2">
                <Label htmlFor="membershipNumber">Membership No.</Label>
                <Input 
                    id="membershipNumber" 
                    value={membershipNumber}
                    onChange={(e) => setMembershipNumber(e.target.value)}
                    placeholder="Enter membership number"
                />
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader>
            <CardTitle>Add New Medication</CardTitle>
            <CardDescription>Add a new drug to check for interactions against your current list.</CardDescription>
          </CardHeader>
          <form onSubmit={handleAddDrug}>
            <CardContent>
              <Input
                placeholder="e.g., Aspirin"
                value={newDrugName}
                onChange={(e) => setNewDrugName(e.target.value)}
                disabled={isLoading}
              />
            </CardContent>
            <CardFooter>
              <Button type="submit" disabled={isLoading} className="w-full">
                {isLoading ? <Loader2 className="animate-spin mr-2" /> : <PlusCircle className="mr-2" />}
                Add and Check
              </Button>
            </CardFooter>
          </form>
        </Card>
      </div>

      <div className="lg:col-span-2">
        <Card>
          <CardHeader>
            <CardTitle>My Medications</CardTitle>
            <CardDescription>
              A list of your current medications. Interaction alerts will appear here.
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            {medications.map((med) => (
               <Card key={med.id} className="p-4">
                 <div className="flex justify-between items-start">
                    <div>
                      <h3 className="font-bold text-lg flex items-center gap-2">
                        <Pill size={20} /> {med.name}
                        {getInteractionStatus(med.name) && (
                            <Badge variant="destructive" className="flex items-center gap-1">
                                <AlertCircle size={14} /> Interaction
                            </Badge>
                        )}
                      </h3>
                      <p className="text-sm text-muted-foreground">
                        {med.dosage} - {med.frequency} - {med.form}
                      </p>
                    </div>
                    <Button variant="ghost" size="icon" onClick={() => removeDrug(med.id)} className="text-muted-foreground hover:text-destructive">
                        <Trash2 size={16} />
                    </Button>
                 </div>
                <Accordion type="single" collapsible className="w-full">
                    <AccordionItem value="item-1" className="border-b-0">
                        <AccordionTrigger className="text-sm pt-2 pb-0">View Details</AccordionTrigger>
                        <AccordionContent className="text-muted-foreground space-y-1 pt-2 text-sm">
                           <p className="flex items-center gap-2"><Hash size={14}/> <strong>RX Number:</strong> {med.rxNumber}</p>
                           <p className="flex items-center gap-2"><Stethoscope size={14} /> <strong>Prescribed by:</strong> {med.doctor}</p>
                           <p className="flex items-center gap-2"><Pill size={14} /> <strong>Pill Count:</strong> {med.pills}</p>
                        </AccordionContent>
                    </AccordionItem>
                </Accordion>
              </Card>
            ))}
            {medications.length === 0 && (
                <div className="text-center py-8 text-muted-foreground">
                    You have no medications saved.
                </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
